.App {
  text-align: center;
}

.image{
  position: relative;
  height: 100vh;

}
.content {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  height: 100vh;
  width: 100%;
}

.img-txt{
  background-color: #333;
  text-transform: uppercase;
  color: #fff;
  padding: 1rem;
  font-size: 1.5rem;
  letter-spacing: 10px;
}

h3 {
  letter-spacing: 6px;
  text-transform: uppercase;
  font-size: 1.3rem;
  text-align: center;
}

.text-box{
  text-align: center;
  padding: 3rem 1rem;
  text-align: justify;
}